import { Navigate } from 'react-router-dom';
import { useAuth } from './hooks';

const PrivateRoute = ({ component: Component, redirectTo = '/' }) => { // component - ContactsPage
  const { isLoggedIn, isRefreshing } = useAuth(); // isLoggedIn - true or false
  const shouldRedirect = !isLoggedIn && !isRefreshing; //  isLoggedIn = false and isRefreshing = false,  shouldRedirect = true
  return shouldRedirect ? <Navigate to={redirectTo} /> : <Component/>; // if shouldRedirect is true,  redirect to redirectTo,   Component
};

export default PrivateRoute;